<template>
  <div class="gift" id="gift">
    <Header :leftClick="moniBack" v-if="!skeletonLoading && inActivity && deliveryCodeChecked && codeCheck"></Header>
    <skeleton v-if="skeletonLoading" :nums="4"></skeleton>
    <div v-else class="detail-content" :class="{'no-header':!(!skeletonLoading && inActivity && deliveryCodeChecked && codeCheck)}">
        <template v-if="inActivity">
            <div v-if="!deliveryCodeChecked" class="content">
                <h4>请输入提货卡编码</h4>
                <div class="input">
                    <van-field v-model="formData.code" type="digit" :maxlength="6" placeholder="点此输入" />
                </div>
                <div class="confirm-btn" @click="checkoutDeliveryCode">
                    <span>确定</span>
                </div>
            </div>
            <div v-else class="detail">
                <span class="code" v-if="codeCheck">提货卡编码：{{formData.code}}</span>
                <div class="card">
                    <van-field label-class="input-label" v-model="formData.userName" @input="nameInput" label="姓名" placeholder="请填写收货人姓名" />
                    <div class="input-devider"></div>
                    <van-field label-class="input-label" v-model="formData.phone" @input="phoneInput" type="digit" label="手机号" placeholder="请填写收货人手机号" />
                    <div class="input-devider"></div>
                    <van-field label-class="input-label" v-model="formData.area" @click="toLocation" label="地区" readonly right-icon="arrow" placeholder="请选择省市区"/>
                    <div class="input-devider"></div>
                    <!-- <van-field label-class="input-label" class="address-detail" v-model="formData.address" @input="addressInput" label="详细地址" placeholder="详细地址，例1层101室"/> -->
                    <van-field label-class="input-label" class="address-detail" v-model="formData.address" rows="1" autosize type="textarea" @input="addressInput" label="详细地址" placeholder="详细地址，例1层101室" />
                    <div class="input-devider" v-if="codeCheck"></div>
                    <div class="camera" v-if="codeCheck">
                        <div class="title input-label">上传图片</div>
                        <div class="photo-area">
                            <div class="upload">
                                <van-uploader
                                    v-model="images"
                                    :before-read="beforeRead"
                                    :after-read="uploadImg"
                                    :max-size="5 * 1024 * 1024"
                                    @oversize="onOversize"
                                    :max-count="1">
                                    <img class="camera-icon" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/gift/gift-comera.svg"/>
                                    <div class="camera-des">点此上传</div>
                                </van-uploader>
                            </div>
                            <div class="upload-tips">请拍摄提货卡编码页的照片并上传</div>
                        </div>
                    </div>
                </div>
                <div class="submit-btn" @click="beforeSubmitInfo"><span>提交信息</span></div>
            </div>
        </template>
        <tips v-else :startTime="startTime" :endTime="endTime"></tips>
        <!-- 级联省市区选择弹窗 -->
        <van-popup v-model="showLocation" position="bottom" @close="onHide" @click="onHide">
            <change-geographic-location ref='geoLocation' :onlyread="true"  @onHide="onHide" @handleLocation="handleLocation" />
        </van-popup>
        <!-- 提货码已申请过弹窗 -->
        <van-popup v-model="showApplied" style="width:calc(100% - 1.4rem);border-radius: .16rem;" @close="onAppliedHide" >
            <div class="applied-container">
                <div class="applied-content">此编码已提交过申请，谢谢您的支持</div>
                <div class="applied-btn" @click="onAppliedHide">确定</div>
            </div>
        </van-popup>
         <!-- 提交信息确认弹窗 -->
        <van-popup v-model="showConfirm" style="width:calc(100% - 1.4rem);border-radius: .16rem;" @close="onConfirmHide" >
            <div class="confirm-container">
                <div class="confirm-content">
                    <h4 class="confirm-title">请确认信息是否无误</h4>
                    <div class="items">
                        <div class="confirm-item">
                            <div class="label">姓名</div>
                            <div class="value">{{formData.userName}}</div>
                        </div>
                        <div class="confirm-item">
                            <div class="label">手机号</div>
                            <div class="value">{{formData.phone}}</div>
                        </div>
                        <div class="confirm-item">
                            <div class="label">收货地址</div>
                            <div class="value">{{formData.area + ' ' + formData.address}}</div>
                        </div>
                    </div>
                </div>
                <div class="confirm-btns">
                    <div class="btn edit" @click="onConfirmHide">返回修改</div>
                    <div class="btn submit" @click="submitInfo">提交</div>
                </div>
            </div>
        </van-popup>
    </div>
  </div>
</template>
<script>
import skeleton from '@/components/skeleton'
import tips from './components/tips'
import Header from '@/components/header'
/*
 * 省市区级联弹窗
 */
import changeGeographicLocation from '@/components/change-geographic-location/change-geographic-location'
import {
  checkEvent,
  checkCode,
  submitInfo,
  uploadFileIn
} from '@/http/service/gift'

export default {
  components: {
    skeleton,
    tips,
    changeGeographicLocation,
    Header
  },
  data() {
    return {
      skeletonLoading: true,
      id: null, // 活动id
      inActivity: false, // 是否在活动中
      startTime: '', // 活动开始时间
      endTime: '', // 活动结束时间
      deliveryCodeChecked: false, // 提货码校验状态
      showLocation: false, // 省市区选择弹窗
      showApplied: false, // 提货码已申请过弹窗弹窗
      showConfirm: false, // 提交信息确认弹窗
      codeCheck: true, // 是否校验提货码
      images: [], // 上传图片列表
      formData: {
        code: '', // 提货码
        userName: '', // 姓名
        phone: '', // 手机号
        area: '', // 地区
        address: '', // 详细地址
        picture: '', // 图片链接
        eventId: '' // 活动id
      },
      inputBak: {
        name: '',
        address: '',
        phone: ''
      }

    }
  },
  created() {
    this.id = this.$route.query.id
    this.formData.eventId = this.id
    document.title = '体验产品领取'
    this.checkActivity()
  },
  mounted() {
  },
  watch: {
    '$route' (to, from) {
      this.$router.go(0);
    }
  },
  methods: {
    phoneInput(val) {
      if (val.length < 11) {
        this.inputBak.phone = val
      } else if (val.length === 11) {
        this.inputBak.phone = val
        if (!/^1([3456789][0-9]|4[01356789]|66|9[89])\d{8}$/.test(this.formData.phone)) {
          this.$toast('请填写正确的手机号');
        }
      } else {
        this.formData.phone = this.inputBak.phone
        if (!/^1([3456789][0-9]|4[01356789]|66|9[89])\d{8}$/.test(this.formData.phone)) {
          this.$toast('请填写正确的手机号');
        }
      }
    },
    nameInput(val) {
      if (this.getLength(val) > 240) {
        this.formData.userName = this.inputBak.name
      } else {
        this.inputBak.name = val
      }
    },
    addressInput(val) {
      if (this.getLength(val) > 140) {
        this.formData.address = this.inputBak.address
      } else {
        this.inputBak.address = val
      }
    },
    /**
       * @description 校验提货码是否有效
      */
    checkoutDeliveryCode() {
      if (!this.formData.code) {
        this.$toast('请输入提货码');
        return
      }
      if (this.formData.code.length > 6 || isNaN(this.formData.code)) {
        this.$toast('提货码只能是6位数字');
        return
      }
      checkCode({ code: this.formData.code, id: this.id }).then(res => {
        if (res.data.success) {
          if (res.data.data) {
            this.deliveryCodeChecked = true
          } else {
            this.onAppliedShow()
          }
        } else {
          this.$toast('提货码校验异常');
        }
      })
    },
    /**
       * @description 检查活动是否开启
      */
    checkActivity() {
      checkEvent({ id: this.id }).then(res => {
        if (res.data.success) {
          if (res.data.data.success) {
            this.inActivity = true
            if (res.data.data.code === '4005') {
              this.codeCheck = false // 不校验提货码
              this.deliveryCodeChecked = true // 状态改为校验失败
            }
            this.skeletonLoading = false
          } else {
            switch (res.data.data.code) {
              case '4001':
                this.$toast('活动不存在');
                break;
              case '4002':
                // this.startTime = new Date(res.data.data.startTime).toLocaleDateString()
                this.startTime = res.data.data.startTime
                this.skeletonLoading = false
                break;
              case '4003':
                // this.endTime = new Date(res.data.data.endTime).toLocaleDateString()
                this.endTime = res.data.data.endTime
                this.skeletonLoading = false
                break;
            }
          }
        } else {
          this.$toast('活动信息校验失败');
        }
        console.log('res=========>', res)
      })
    },
    /**
     * @description 提交表单信息
    */
    submitInfo() {
      let param = {
        pickUpGoodsCode: this.formData.code, // 提货码
        userName: this.formData.userName, // 姓名
        phone: this.formData.phone, // 手机号
        area: this.formData.area, // 地区
        address: this.formData.address, // 详细地址
        picture: this.formData.picture, // 图片链接
        giftCardId: this.formData.eventId // 活动id
      }
      submitInfo(param).then(res => {
        if (res.data.success) {
          if (res.data.data.success) {
            this.$router.push({ path: '/gift-success' });
          } else {
            switch (res.data.data.code) {
              case '4003':
              // 活动已结束
                this.inActivity = false
                this.startTime = ''
                // this.endTime = new Date(res.data.data.endTime).toLocaleDateString()
                this.endTime = res.data.data.endTime
                break;
              default:
                this.$toast('提交信息失败');
                break;
            }
          }
        } else {
          this.$toast('提交信息失败');
        }
      })
    },
    /**
       * @description 提交详细信息
      */
    beforeSubmitInfo() {
      if (!this.checkParam()) return
      // 输入校验通过 二次确认
      this.onConfirmShow()
    },
    /**
     * @description 检查提交信息是否合法
    */
    checkParam() {
      if (!this.formData.userName) {
        this.$toast('请输入姓名');
        return false
      }
      if (!/^1([3456789][0-9]|4[01356789]|66|9[89])\d{8}$/.test(this.formData.phone)) {
        this.$toast('请填写正确的手机号');
        return false
      }
      if (!this.formData.area) {
        this.$toast('请选择地区');
        return false
      }
      if (!this.formData.address) {
        this.$toast('请输入详细地址');
        return false
      }
      if (!this.formData.picture && this.codeCheck) {
        // 不校验提货码活动取消对上传图片的校验
        this.$toast('请拍摄提货卡编码页的照片并上传');
        return false
      }
      return true
    },
    /**
     * @description 获取字节数
    */
    getLength(val) {
      let str = val + '';
      let bytesCount = 0;
      for (let i = 0, n = str.length; i < n; i++) {
        let c = str.charCodeAt(i);
        if ((c >= 0x0001 && c <= 0x007e) || (c >= 0xff60 && c <= 0xff9f)) {
          bytesCount += 1;
        } else {
          bytesCount += 2;
        }
      }
      return bytesCount;
    },
    /**
       * @description 打开地址选择器
      */
    toLocation() {
      this.showLocation = true
    },
    /**
     * @description 点击选择所在地区 确认关闭回调
     */
    handleLocation(data) {
      this.showLocation = false
      if (!data) return
      this.formData.area = data.province + ' ' + data.city + ' ' + data.district + ' ' + data.township
    },
    /**
     * @description 点击选择所在地区 取消关闭回调
     * @param
     */
    onHide() {
      this.showLocation = false
      this.$refs.geoLocation.load()
    },
    /**
     * @description  文件读取前
    */
    beforeRead(file) {
      if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
        this.$toast('请上传正确的图片格式');
        return false;
      }
      return true;
    },
    /**
     * @description  传本地完成后
    */
    uploadImg(file) {
      console.log('file:', file)
      console.log('this.images:', this.images)
      if (this.images.length !== 0) {
        this.images[0].status = 'uploading'
        this.images[0].message = '上传中'
      }
      const data = new FormData();
      data.append('file', this.images[0].file);
      uploadFileIn(data).then(res => {
        if (res.data.success) {
          this.formData.picture = res.data.data
          if (this.images.length !== 0) {
            this.images[0].status = 'done'
          }
        } else {
          if (this.images.length !== 0) {
            this.images[0].status = 'failed'
            this.images[0].message = '上传失败'
          }
          this.$toast('图片上传失败');
        }
      })
    },
    onAppliedShow() {
      this.showApplied = true
    },
    onAppliedHide() {
      this.showApplied = false
    },
    onConfirmShow() {
      this.showConfirm = true
    },
    onConfirmHide() {
      this.showConfirm = false
    },
    onOversize(file) {
      this.$toast('图片大小限制5M以下');
    },
    /**
     * @description 压缩图片文件到指定大小
    */
    imgChangeSizeTo(currentFile, targetSize) {
      return new Promise((resolve, reject) => {
        if (!/\/(?:jpeg|png)/i.test(currentFile.type)) {
          this.$toast('请选择正确的图片格式');
          reject(currentFile)
          return
        }
        if (currentFile.size <= targetSize) {
          resolve(currentFile)
          return
        }
        let _URL = window.URL || window.webkitURL;
        let radio = targetSize / currentFile.size
        let canvas = document.createElement('canvas')
        let context = canvas.getContext('2d')
        let img = new Image()
        img.src = _URL.createObjectURL(currentFile)
        img.onload = () => {
        // 指定canvas画布大小，该大小为最后生成图片的大小
          canvas.width = img.width * radio
          canvas.height = img.height * radio
          context.drawImage(img, 0, 0, canvas.width, canvas.height)
          // 将绘制完成的图片重新转化为base64编码，currentFile.type为图片类型，1为默认压缩质量
          let blob = this.dataURLtoBlob(canvas.toDataURL(currentFile.type, 1));
          // blob转file
          let file = new window.File([blob], currentFile.name, { type: currentFile.type })
          console.log('imgChangeSizeTo', file)
          resolve(file)
        }
      })
    },
    /**
     * @description  将base64转换为blob
    */
    dataURLtoBlob: function(dataurl) {
      var arr = dataurl.split(',');
      var mime = arr[0].match(/:(.*?);/)[1];
      var bstr = atob(arr[1]);
      var n = bstr.length;
      var u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    /**
     * 模拟返回
    */
    moniBack() {
      if (this.inActivity && this.deliveryCodeChecked) {
        location.href = document.URL
      } else {
        this.$store.dispatch('doHistoryBack')
      }
    },
  },
}
</script>

