/**
 * 示例文件
 * 可单独封装接口请求方法
 * 文件根据相应功能模块语义化命名
 * 导出至'@/http/index'
 */
import http from '../http'
import url from '../url'

// 获取省市区街道
export const changeGeographicLocation = (params) => {
  return http.request({
    url: url.GET_PROVINCE_LIST,
    method: 'get',
    params
  })
}

// 获取授权范围内省市区街道
export const getRegionByStoreOwnerIdAndReType = (params) => {
  return http.request({
    url: url.GET_PROVINCE_LIST_BY_STOREOWNERID,
    method: 'get',
    params
  })
}
