/**
 * 店铺相关
 */
import URL from '../url.js'
import http from '../http'
// import { get } from '../http'

// import config from '@/config'

// 校验活动是否存在,是否在有效期
// export const checkEvent = data => get(URL.checkEvent, data)
export const checkEvent = (data) => {
  return http.request({
    url: URL.checkEvent,
    method: 'GET',
    params: data,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}
// 校验提货码是否有效
// export const checkCode = data => get(URL.checkCode, data)
export const checkCode = (data) => {
  return http.request({
    url: URL.checkCode,
    method: 'GET',
    params: data,
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}
// 礼品卡提取提交信息
// export const submitInfo = data => post(URL.submitInfo, data)
export const submitInfo = (data) => {
  return http.request({
    url: URL.submitInfo,
    method: 'POST',
    data,
    headers: {
      'Content-Type': 'application/json'
    }
  })
}
// 上传图片
// export const uploadFileIn = data => post(URL.uploadFileIn, data)
export const uploadFileIn = (data) => {
  return http.request({
    url: URL.uploadFileIn,
    method: 'POST',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}
