<template>
  <div class="box">
    <!-- <van-popup v-model="show" position="bottom" @close="closeModal" @click="closeModal"> -->
      <div class="address" @click.stop>
        <div class="top" @touchmove.prevent>
          <div class="title">配送至
          <!-- <img v-on:click="closeModal" src="@/assets/imgs/search/addressClose.png" alt="" /> -->
           <!-- <img v-on:click="closeModal" src="@/assets/imgs/common/control-close.png" alt="" /> -->
          <img v-on:click="closeModal" :src="`${$cdnImageUrl}/common/control-close.png`" alt="" />
          </div>
          <div v-if="location" class="nowAddress" @click="chooseCurrent">
            <img src="@/assets/imgs/search/location.png" />
            <span>定位地址({{location.district}}/{{location.township}})</span>
          </div>
          <div v-else class="nowAddress" @click="chooseCurrent">
            <img :src="`${$cdnImageUrl}/common/location-icon-blue.svg`" />
            <span>点击自动定位</span>
          </div>
          <div class="change">
            <span @click="provinceClick" v-if="province">{{ provinceData.text }}</span>
            <span @click="cityClick" v-if="city">{{ cityData.text }}</span>
            <span @click="areaClick" v-if="area">{{ areaData.text }}</span>
            <span class="placeChange">请选择</span>
          </div>
        </div>
        <div class="bottom" v-if="list.length > 6">
          <van-loading v-if="showLoading" class="loadingClass" vertical>加载中...</van-loading>
          <div class="address-item" @click="cityListClick(i)" v-for="(i, k) in list" :key="k">{{ i.text }}</div>
        </div>
        <div class="bottom" @touchmove.prevent v-else>
          <van-loading v-if="showLoading" class="loadingClass" vertical>加载中...</van-loading>
          <div class="address-item" @click="cityListClick(i)" v-for="(i, k) in list" :key="k">{{ i.text }}</div>
        </div>
      </div>
    <!-- </van-popup> -->
  </div>
</template>

<script>
import { mapMutations, mapActions } from 'vuex'
import { changeGeographicLocation } from '@/http/service/change-address'
/**
 * 选择地址弹窗
 * 使用标签<change-geographic-location>
 * 关闭回调 onHide
 * 选择位置回调 handleLocation
 */
export default {
  props: ['onlyread'], // onlyread 只读属性 不修改store不进行localStroage存储
  data() {
    return {
      showLoading: false,
      location: null, // 定位信息
      show: true,
      list: [], // 城市数据存放
      windowHeight: '', // 获取屏幕高度
      province: false, // 控制省点击出现后的盒子
      provinceData: '', // 存放省的点击内容
      city: false, // 控制城市点击出现后的盒子
      cityData: '', // 存放城市的点击内容
      area: false, // 控制区点击出现的盒子
      areaData: false, // 存放区的点击内容
      clickNum: 0, // 计算点击次数
      townData: '', // 存放镇的点击内容
      firstValue: 0, // 存放第一次点击的当前数据的id
      secondValue: 0, // 存放第二次点击的当前数据的id
    }
  },
  methods: {
    ...mapMutations(['setLocationInfo', 'setLoaction']),
    ...mapActions(['getLatLongByLocationInfo']),
    hideParent(e) {
      // document.body.classList.add('van-overflow-hidden')
      // document.documentElement.style.overflow = 'hidden'
    },
    removeHidden(e) {
      // document.body.classList.remove('van-overflow-hidden')
      // document.documentElement.style.overflow = 'auto'
    },
    load() {
      this.hideParent()
      this.showLoading = true

      /**
     * @description 初次渲染拿到所有城市列表数据
     * @params 请求参数
     */
      const c = {
        parentId: 0,
        regionType: 0,
      }
      changeGeographicLocation(c)
        .then(res => {
          this.showLoading = false
          if (res.status === 200) {
            this.list = res.data.data
          } else {
            this.$toast('请求失败，请重试')
          }
        })
        .catch(res => {
          this.$toast('请求失败，请重试')
        })
    },
    /**
     * 选择定位地址
     */
    chooseCurrent() {
      if (this.location) {
        if (!this.onlyread) {
          localStorage.setItem('latitude', this.location.latitude)
          localStorage.setItem('longitude', this.location.longitude)
          localStorage.setItem('provinceId', this.location.provinceId)
          localStorage.setItem('cityId', this.location.cityId)
          localStorage.setItem('districtId', this.location.districtId)
          localStorage.setItem('townshipId', this.location.townshipId)
          // localStorage.setItem('location', JSON.stringify(this.location));
          this.setLocationInfo(this.location)
          this.setLoaction({
            latitude: this.location.latitude,
            longitude: this.location.longitude
          })
        }
        this.$emit('handleLocation', this.location)
        this.closeModal()
      } else {
        console.log('手动定位')
        this.getLocation()
      }
    },
    /**
     * @description 关闭弹窗
     */
    closeModal() {
      this.$emit('handleLocation', this.location)
      this.$emit('onHide')
    },
    /**
     * @description 点击城市列表后显示的省级盒子点击事件
     * @params 传递初始化的获取列表的参数
     */
    provinceClick() {
      this.showLoading = true
      this.list = []
      changeGeographicLocation({ parentId: 0, regionType: 0 })
        .then(res => {
          this.province = false // 自己关闭
          this.city = false // 城市按钮关闭
          this.area = false // 区按钮关闭
          this.clickNum = 0 // 点击技术清零
          this.showLoading = false
          if (res.status === 200) {
            this.list = res.data.data
          } else {
            this.$toast('请求失败，请重试')
          }
        })
        .catch(res => {
          this.$toast('请求失败，请重试')
        })
    },
    /**
     * @description 点击城市列表后显示的市级盒子点击事件
     * @params 传递初始化的获取列表的参数
     */
    cityClick() {
      this.showLoading = true
      this.list = []
      changeGeographicLocation({ parentId: this.firstValue, regionType: 1 })
        .then(res => {
          this.city = false // 自己关闭
          this.area = false // 区按钮关闭
          this.clickNum = 1
          this.showLoading = false
          if (res.status === 200) {
            this.list = res.data.data
          } else {
            this.$toast('请求失败，请重试')
          }
        })
        .catch(res => {
          this.$toast('请求失败，请重试')
        })
    },
    /**
     * @description 点击城市列表后显示的某区的盒子点击事件
     * @params 传递初始化的获取列表的参数
     */
    areaClick() {
      this.showLoading = true
      this.list = []
      changeGeographicLocation({ parentId: this.secondValue, regionType: 2 })
        .then(res => {
          this.clickNum = 2
          this.area = false // 自己消失
          this.showLoading = false
          if (res.status === 200) {
            this.list = res.data.data
          } else {
            this.$toast('请求失败，请重试')
          }
        })
        .catch(res => {
          this.$toast('请求失败，请重试')
        })
    },
    /**
     * @description 城市列表的点击事件，
     * @params {boolean} 根据点击计数判断当前的点击事件，进行不同的存值，获取数据
     */
    cityListClick(i) {
      this.showLoading = true
      if (this.clickNum === 0) {
        this.provinceData = i
        this.list = []
        const a = {
          parentId: i.value,
          regionType: 1,
        }
        this.firstValue = i.value
        changeGeographicLocation(a)
          .then(res => {
            this.showLoading = false
            if (res.status === 200) {
              this.list = res.data.data
            } else {
              this.$toast('请求失败，请重试')
            }
          })
          .catch(res => {
            this.$toast('请求失败，请重试')
          })
        this.province = true
        this.clickNum++
      } else if (this.clickNum === 1) {
        this.list = []
        this.cityData = i
        changeGeographicLocation({ parentId: i.value, regionType: 2 })
          .then(res => {
            this.showLoading = false
            if (res.status === 200) {
              this.list = res.data.data
            } else {
              this.$toast('请求失败，请重试')
            }
          })
          .catch(res => {
            this.$toast('请求失败，请重试')
          })
        this.secondValue = i.value
        this.city = true
        this.clickNum++
      } else if (this.clickNum === 2) {
        this.list = []
        this.area = true
        this.areaData = i
        changeGeographicLocation({ parentId: i.value, regionType: 3 })
          .then(res => {
            this.showLoading = false
            if (res.status === 200) {
              this.list = res.data.data
            } else {
              this.$toast('请求失败，请重试')
            }
            if (res.data.data.length === 0) {
              this.clickNum = 0
              this.resetLocation()
            }
          })
          .catch(res => {
            this.$toast('请求失败，请重试')
          })
        this.clickNum++
      } else if (this.clickNum === 3) {
        this.list = []
        this.townData = i
        this.clickNum = 0

        this.resetLocation()

        // localStorage.setItem(
        //   'location',
        //   JSON.stringify({
        //     region: `${this.provinceData.text} ${this.cityData.text} ${this.areaData.text} ${this.townData.text}`,
        //     provinceId: this.provinceData.value,
        //     province: this.provinceData.text,
        //     cityId: this.cityData.value,
        //     city: this.cityData.text,
        //     districtId: this.areaData.value,
        //     district: this.areaData.text,
        //     townshipId: this.townData.value,
        //     township: this.townData.text,
        //     longitude: this.location.longitude,
        //     latitude: this.location.latitude,
        //   })
        // )
        // this.$emit('handleLocation', {
        //   region: `${this.provinceData.text} ${this.cityData.text} ${this.areaData.text} ${this.townData.text}`,
        //   provinceId: this.provinceData.value,
        //   province: this.provinceData.text,
        //   cityId: this.cityData.value,
        //   city: this.cityData.text,
        //   districtId: this.areaData.value,
        //   district: this.areaData.text,
        //   townshipId: this.townData.value,
        //   township: this.townData.text,
        // })
      }
    },
    /**
     * 切换全局地理定位
     */
    resetLocation() {
      let locationInfo = {
        region: `${this.provinceData.text} ${this.cityData.text} ${this.areaData.text} ${this.townData.text}`,
        provinceId: this.provinceData.value,
        province: this.provinceData.text,
        cityId: this.cityData.value,
        city: this.cityData.text,
        districtId: this.areaData.value,
        district: this.areaData.text,
        townshipId: this.townData.value,
        township: this.townData.text,
      }

      this.getLatLongByLocationInfo(locationInfo).then(res => {
        if (res === 400) {
          this.$toast('根据位置信息获取经纬度失败')
        } else {
          locationInfo.latitude = res.latitude;
          locationInfo.longitude = res.longitude;

          if (!this.onlyread) {
            localStorage.setItem('latitude', res.latitude)
            localStorage.setItem('longitude', res.longitude)
            localStorage.setItem('provinceId', locationInfo.provinceId)
            localStorage.setItem('cityId', locationInfo.cityId)
            localStorage.setItem('districtId', locationInfo.districtId)
            localStorage.setItem('townshipId', locationInfo.townshipId)
            // localStorage.setItem('location', JSON.stringify(locationInfo));
            this.setLocationInfo(locationInfo)
            this.setLoaction(res)
          }

          this.$emit('handleLocation', locationInfo)
        }
      })
    },
    /**
     * @description 从store获取经纬度和地理位置
     */
    getLocation() {
      this.$store.dispatch('doLocation', true).then(res => {
        if (res === 400) {
          this.$toast('定位失败，请自行选择位置信息')
        } else {
          this.$toast('自动定位成功')
          this.$store.dispatch('doLocationInfo', { latitude: res.latitude, longitude: res.longitude, noStore: this.onlyread, force: true }).then(result => {
            this.location = result
            this.closeModal()
          })
        }
      })
    },
  },
  mounted() {
    this.load()
  },
  beforeDestroy() {
    this.removeHidden()
  },
}
</script>

